<template>
  <div class="rightContent">
    <div class="navTile">
      当前位置：
      <router-link :to="{name:'Main'}">首页</router-link>
      &nbsp;>>&nbsp;
      <router-link :to="{name:'Resource'}">教学资源</router-link>
      &nbsp;>>&nbsp;
      <router-link :to="{name:'Videos'}">教学视频</router-link>
    </div>
    <div class="content" v-loading="loading" element-loading-text="拼命加载中">
      <ul class="itemUl">
        <div v-if="List.length > 0">
          <li v-for="(list,index) in List" :key="index">
            <router-link :to="{name:'VideoDetail',query:{videoId:list.videoId}}" class="overPointr1">
              {{list.videoName}}
            </router-link>
            <span class="time">{{list.videoTime}}</span>
          </li>
        </div>
        <div class="emptyVideos" v-else>
          暂无教学视频
        </div>
      </ul>
      <div class="page">
        <el-pagination
          @current-change="handleCurrentChange"
          :current-page.sync="pageNum"
          :page-size="pageSize"
          :hide-on-single-page="page.pages < 2"
          layout="total, prev, pager, next"
          :total="page.total">
        </el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
  import {REQVIDEO} from '../../../../../api/types'
  import merge from 'webpack-merge'  //用于动态修改地址栏参数的
  import {mapActions} from 'vuex'
  export default {
    name: '',
    data () {
      return {
        List:[],                 //资源列表
        pageNum:1,               //当前页数
        pageSize:10,             //当前页行数
        showFlag:1,              //显示标志，1显示，0废弃
        page:{                   //页数等信息
          total:0,               //总条数
          size:0,                //当页的行数
          pages:0,               //总页数
        },
        loading:false,           //是否加载中
      }
    },
    methods: {
      ...mapActions(['choiceMenuId','choiceTypeMenuId']),

      //分页
      handleCurrentChange(num){
        this.pageNum = num
        this.reqVideos()
      },
      //获取视频列表
      async reqVideos(){
        var {pageNum,pageSize,showFlag} = this
        var params = {pageNum,pageSize,showFlag}
        this.loading = true
        var result = await REQVIDEO(params)
        this.loading = false
        if(result && result.status === 0){
          result = result.data
          //视频列表
          this.List = result.videoVOS
          console.log(this.List)
          //更新地址栏参数
          this.chanceUrlQuery({pageNum})
          //更新页码信息
          var {total,size,pages} = result
          this.page = {total,size,pages}
        }
      },

      //消息提示
      message(message,type,duration){
        this.$message({
          message,
          duration,
          type
        })
      },

      //修改路由参数
      chanceUrlQuery(data){
        //清空所有的参数：
        this.$router.push({
          query:merge({},{})
        })
        //更新路由参数
        this.$router.push({
          query:merge(this.$route.query,data)
        })
      },
    },
    mounted () {
      //更新菜单选中项
      this.choiceMenuId('Videos')
      this.choiceTypeMenuId('Videos')
      if(this.$route.query.pageNum){
        this.pageNum = this.$route.query.pageNum * 1
      }
      //获取视频列表
      this.reqVideos()
    },
  }
</script>

<style lang="less">
  .userResource{
    .rightContent{
      .navTile{
        padding-left: 40px;
        height: 45px;
        border-radius: 5px 5px 0 0;
        line-height: 45px;
        color: #fff;
        font-size: 14px;
        background: rgb(85,171,235);
        overflow: hidden;
        a{
          color: white;
          text-decoration: none;
          &:hover{
            text-decoration: underline;
          }
        }
      }
      .content{
        line-height: 24px;
        padding: 10px 15px 15px;
        border: 1px solid #CECECE;
        border-top: 0;
        ul.itemUl{
          padding: 0;
          min-height: 470px;
          margin: 0;
          li{
            list-style: none;
            height: 45px;
            line-height: 45px;
            border-bottom: 1px dashed #D7D7D7;
            padding-left: 15px;
            &:before,&:after{
              content: '';
              display: table;
              clear: both;
            }
            a{
              float: left;
              display: inline-block;
              color: #333;
              max-width: calc(100% - 100px);
              &:hover{
                text-decoration: none;
                color: red;
              }
            }
            .time{
              float: right;
              color: #999;
            }
          }
          .emptyVideos{
            color: #333;
            text-indent: 2em;
            font-size: 14px;
            line-height: 23px;
            min-height: 100px;
          }
        }
        .page{
          text-align: center;
        }
      }
    }
  }
  @media screen and (max-width: 767px){
    .userResource{
      .rightContent{
        .navTile{
          padding-left: 10px;
        }
        .content{
          padding-top: 5px;
          ul{
            li{
              font-size: 14px;
              padding-left: 0px;
              &:before,&:after{
                content: '';
                display: table;
                clear: both;
              }
              a{
                float: left;
                display: inline-block;
                color: #333;
                max-width: calc(100% - 100px);
                &:hover{
                  text-decoration: none;
                  color: red;
                }
              }
              .time{
                float: right;
                color: #999;
              }
            }
          }
        }
      }
    }
  }
</style>



